  --dominant-text-color: #fff;
  --alternative-text-color: #907e82;
  --usual-text-color: #000;
  --accent-backgroung-color: #ffb8ca;
  --title-accent-color: #ffa5ba;
  --slider-color: #d31342;
  --inactive-slider-color: #d9d9d9;
  --agency-color: #f0d1a5;
  --alternative-agency-color: #c2e297;
  --quotes-color: #e1e1e1;
  --point-review-color: #c7133e;
  --sosials-inactive-color: #fea5bb;
  --backgroung-color: #fff5f6;
  --border-mobile-menu-color: #e18298;
  --background-mobile-menu-color: #e17992;
  --button-hover-gray: #ecebeb;
  --slider__text: #fafafa;
}

body {
  font-family: var(--primary-font-family), Verdana, Tahoma, arial, sans-serif;
  background-color: var(--dominant-text-color);
  color: var(--usual-text-color);
  margin: 0;
  font-weight: 400;
}

body.modal-open {
  overflow: hidden;
}

button {
  font: inherit;
  color: inherit;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.title-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 99;
  opacity: 1;
  visibility: visible;
  transition-property: opacity, transform, visibility;
  transition-duration: .4s;
  transition-timing-function: earse;
  background: rgba(227, 129, 236, .3);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
  transform: scale(1);
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: scale(.5);
}

.backdrop.is-hidden .mobile-menu {
  transform: translate(100%);
}

.backdrop-mobile {
  width: 100%;
  height: 100%;
  z-index: 99;
  transition-property: transform, visibility;
  transition-duration: .4s;
  transition-timing-function: earse;
  background: rgba(227, 129, 236, .3);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
  transform: scale(1);
}

@media screen and (min-width: 1200px) {
  .backdrop-mobile {
    display: none;
  }
}

.backdrop-mobile.is-open, .backdrop-mobile.is-mobile {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: scale(.5);
}

.slick-dots {
  justify-content: center;
  align-items: center;
  gap: 18px;
  margin-top: 16px;
